      h1 Geografía turística: clima, biodiversidad, accesibilidad y conectividad
   
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La geografía turística estudia los factores naturales, espaciales y territoriales que influyen en la configuración de los destinos turísticos y en la experiencia de los visitantes. Comprender estos elementos permite planificar y operar servicios turísticos de manera sostenible, garantizando un uso adecuado de los recursos y una oferta coherente con las expectativas de los turistas.
      p En este contexto, el manejo de herramientas de análisis geográfico cumple un papel fundamental en la planificación y gestión de proyectos turísticos y ecoturísticos, especialmente aquellos que dependen de los servicios ecosistémicos ofrecidos por una zona determinada del territorio nacional.
      p A continuación se explica la forma en que la Ley 388 de 1997 establece particularidades sobre la manera en que debe abordarse el turismo sostenible:

    br    
    .row.justify-content-center(data-aos="fade-right")
      .col-md-10.mb-5.mb-md-0
        LineaTiempoD.letra-acordeon-titulos
          .row(numero="A" titulo="")
            .col-md-2
              figure
                img(src='@/assets/curso/temas/tema1/tema1-ce-pasos1.svg', alt='Texto que describa la imagen')
                
            .col-md-10.mb-4.mb-md-0
              p El turismo, como actividad económica y social, requiere espacios funcionales y equilibrados que aseguren su viabilidad ambiental, económica y cultural, evitando impactos negativos sobre otros usos del territorio.

          .row(numero="B" titulo="")
            .col-md-2
              figure
                img(src='@/assets/curso/temas/tema1/tema1-ce-pasos2.svg', alt='Texto que describa la imagen')
                
            .col-md-10.mb-4.mb-md-0
              p Por esta razón, en Colombia la Ley 388 de 1997 establece que el turismo debe abordarse desde la perspectiva del ordenamiento territorial.

          .row(numero="C" titulo="")
            .col-md-2
              figure
                img(src='@/assets/curso/temas/tema1/tema1-ce-pasos3.svg', alt='Texto que describa la imagen')
                
            .col-md-10.mb-4.mb-md-0
              p Con el propósito de que los municipios y entidades locales lo regulen y promuevan su desarrollo de manera sostenible y en armonía con el patrimonio cultural y ambiental de la Nación.
          
          .row(numero="D" titulo="")
            .col-md-2
              figure
                img(src='@/assets/curso/temas/tema1/tema1-ce-pasos4.svg', alt='Texto que describa la imagen')
                
            .col-md-10.mb-4.mb-md-0
              p Esta ley también resalta la importancia de un uso equitativo y racional del suelo, así como la participación ciudadana en la toma de decisiones sobre el desarrollo turístico.
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p De acuerdo con lo anterior, resulta indispensable que los profesionales del sector desarrollen la capacidad de analizar e interpretar material cartográfico como insumo técnico para la planificación territorial de proyectos turísticos, contribuyendo así a un modelo de turismo responsable, planificado y coherente con la gestión ambiental. 
      h3 Clima
      p El clima constituye un factor determinante en el turismo, ya que condiciona la estacionalidad de los viajes, el tipo de actividades que se pueden realizar y la percepción de confort por parte del visitante. En el caso de Colombia, su ubicación ecuatorial y diversa topografía le confieren una gran variedad climática, que va desde zonas cálidas en las costas y llanuras hasta climas fríos en las cordilleras andinas. Esta diversidad posibilita el desarrollo de múltiples modalidades turísticas, tales como el turismo de sol y playa en el Caribe, el ecoturismo en la Amazonía o el turismo de montaña en la Sierra Nevada de Santa Marta.
      p A continuación se analiza esta variable desde diferentes datos que facilitan la comprensión de los factores que afectan la experiencia turística y que están directamente relacionados con el clima:

    .row.justify-content-center(data-aos="flip-up")
      .col-md-9.mb-5.mb-md-0
        ImagenInfograficaB.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/infografia/modales/bg-infografia-modales.svg', alt='Texto que describa la imagen')
              
          div(x="30%" y="20%" tooltip="Ver más" numero="1")
            h5 Factores climáticos que afectan el turismo
            p De acuerdo con el IDEAM (2015), la altitud es el factor que más influye en la variación climática del territorio colombiano, generando los pisos térmicos —cálido, templado, frío, páramo y glacial— que diversifican las actividades turísticas y los productos regionales.

          div(x="50%" y="50%" tooltip="Ver más", numero="2")
            h5.mb-2 Factores climáticos que afectan el turismo
            p Colombia, debido a su carácter tropical y a la complejidad de su relieve, es un país altamente sensible a las variaciones climáticas. En consecuencia, el turismo nacional se encuentra especialmente expuesto a los efectos del cambio climático, cuya incidencia varía según la modalidad turística y el entorno geográfico. Así, la vulnerabilidad es muy elevada en el turismo de sol y playa, el turismo de alta montaña y el turismo urbano en grandes capitales.

          div(x="70%" y="80%" tooltip="Ver más", numero="3")
            h5.mb-2 Factores climáticos que afectan el turismo
            p Según el IV Informe del IPCC (2007), el cambio climático proyectado para las próximas décadas no se limita al aumento de la temperatura media global, sino que implica también un incremento en la variabilidad climática y en la frecuencia de fenómenos extremos (olas de calor, lluvias intensas o sequías prolongadas). Esto conlleva una pérdida de confort climático y ambiental, así como alteraciones en los ecosistemas naturales que sustentan la actividad turística.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Como plantean Santos-Lacueva y Saladié (2016), esta interacción entre turismo y cambio climático obliga a actuar en dos direcciones:
      .col-md-9.mb-5.mb-md-0
        p 1. Mitigar la influencia de la actividad turística sobre el cambio climático, reduciendo las emisiones y el consumo energético.
        p 2. Adaptar el sector turístico a los nuevos escenarios de cambio global, garantizando su sostenibilidad futura.

      p La siguiente tabla ilustra cómo el cambio climático afecta a diferentes tipos de turismo, en función de la vulnerabilidad de cada modalidad ante las alteraciones climáticas, según los estudios mencionados anteriormente:

    .row.justify-content-center(data-aos="fade-right")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img24.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      h3 Biodiversidad
      p Colombia es reconocida como el segundo país más biodiverso del mundo, albergando cerca del 10 % de la biodiversidad global (Instituto Humboldt, 2021). Esta extraordinaria riqueza natural constituye un recurso estratégico para el desarrollo del turismo de naturaleza, con atractivos como parques nacionales naturales, reservas forestales, áreas protegidas y ecosistemas únicos —entre ellos los páramos, que presentan altos niveles de endemismo y cumplen funciones ecológicas vitales para la regulación hídrica del país
      p De acuerdo con el Portafolio de Turismo de Naturaleza en Colombia (Ministerio de Ambiente y Desarrollo Sostenible, 2021), el país alberga más de 90 ecosistemas de gran importancia ecológica y turística, entre los cuales se destacan los arrecifes de coral, manglares, nevados, bosques tropicales, sabanas, humedales y desiertos, cada uno con un alto potencial para el desarrollo de experiencias sostenibles de observación, educación ambiental y turismo científico.
      p Asimismo, según el Instituto Humboldt (2017), Colombia ocupa posiciones de liderazgo mundial en diversidad biológica:

    .row.justify-content-center(data-aos="flip-down")
      .col-lg-11
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img25.svg', alt='Texto que describa la imagen')    
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Esta riqueza biológica convierte al país en un destino privilegiado para el ecoturismo, el aviturismo (observación de aves), el turismo científico y otras modalidades que promueven la conservación del patrimonio natural y el desarrollo sostenible de las comunidades locales.
      p La siguiente tabla presenta una comparación entre Colombia y otros países megadiversos, destacando su posición en los rankings globales de biodiversidad y la variedad de especies que sustentan su potencial turístico y ecológico.

    .row.justify-content-center(data-aos="fade-right")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img26.svg', alt='Texto que describa la imagen')
    
    br
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Teniendo en cuenta esta información, Colombia enfrenta grandes retos en materia de conservación del patrimonio natural y cultural, lo que hace indispensable fomentar la conciencia ambiental entre las comunidades locales, los prestadores de servicios turísticos y los actores vinculados al desarrollo territorial.
      p En este contexto, el Estado colombiano ha impulsado programas y estrategias de sostenibilidad, entre los cuales se destaca la iniciativa “Negocios Verdes”, liderada por el Ministerio de Ambiente y Desarrollo Sostenible. Este programa incluye el turismo de naturaleza como una de sus categorías prioritarias, al reconocer su potencial para generar beneficios económicos locales mientras promueve la conservación de la biodiversidad y del capital natural del país.
      p De esta manera, se busca integrar el desarrollo económico con la sostenibilidad ambiental, fortaleciendo la competitividad del sector turístico colombiano bajo criterios de responsabilidad social, eficiencia en el uso de los recursos naturales y respeto por el patrimonio cultural y ecológico de las comunidades anfitrionas.
      p
      h3 Accesibilidad
      p La accesibilidad se refiere a la posibilidad de llegar a los atractivos y destinos turísticos de manera segura, cómoda y adecuada, garantizando la movilidad eficiente de los visitantes. Este concepto abarca tanto la infraestructura vial como los sistemas de transporte terrestre, aéreo y fluvial, además de las condiciones de seguridad y ordenamiento del territorio.
      p En Colombia, existen algunos aspectos fundamentales que han mejorado la accesibilidad a los territorios, dentro de los cuales podemos encontrar:

    TabsA.color-acento-contenido.col-lg-12.justify-content-center(data-aos="fade-right") 
      .tarjeta.p-4(titulo="Infraestructura vial")
        .row
          .col-xl-10
            h3.letra-acordeon-titulos Infraestructura vial
            p.letra-acordeon-titulos Según el Ministerio de Transporte de Colombia (2019), la conectividad vial y la calidad de la infraestructura influyen directamente en la competitividad del sector turístico, ya que determinan la facilidad de acceso de los visitantes nacionales e internacionales a los distintos destinos del país.
      
      .tarjeta.p-4(titulo="Firma del acuerdo de paz")
        .row
          .col-xl-10
            h3.letra-acordeon-titulos Firma del acuerdo de paz
            p.letra-acordeon-titulos La firma del Acuerdo de Paz en 2016 representó un punto de inflexión para el turismo nacional, al permitir el ingreso progresivo a territorios antes inaccesibles y fomentar el desarrollo de nuevas rutas y destinos en departamentos como Caquetá, Meta, Putumayo, Chocó y Guaviare, entre otros.
      
      .tarjeta.p-4(titulo="Percepción de seguridad")
        .row
          .col-xl-12
            h3.letra-acordeon-titulos Percepción de seguridad
            p.letra-acordeon-titulos De acuerdo con ProColombia (2019), en el año de la firma del acuerdo, Colombia recibió cinco millones de turistas internacionales, lo que representó un incremento del 14,5 % respecto al año anterior. Desde entonces, las cifras de visitantes continúan en ascenso, reflejando la respuesta positiva del turismo internacional ante la mejora de la accesibilidad y la percepción de seguridad en el país.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La siguiente tabla presenta el incremento de la accesibilidad turística y del flujo de visitantes internacionales, asociado al contexto posterior a la firma del proceso de paz y a la ampliación de la infraestructura de transporte y conectividad nacional.

    .row.justify-content-center 
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema1/tema1-ce-img27.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Nota: sobre 2017-2019: El documento no proporciona una serie completa, pero datos históricos muestran que el crecimiento se vio afectado por otros factores macroeconómicos, aunque la accesibilidad siguió mejorando.

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-md-4
            figure
              img(src='@/assets/curso/temas/tema1/tema1-ce-img28.svg', alt='Texto que describa la imagen')

      .col-md-8.mb-4.mb-md-0.my-4(data-aos="fade-right")
        h3 Conectividad
        p La conectividad constituye un componente esencial del desarrollo turístico, ya que determina el grado en que un destino puede interactuar, recibir visitantes y comunicarse con el mundo exterior. Este concepto complementa la accesibilidad, pues no solo se refiere a la posibilidad de llegar físicamente a un lugar, sino también a la capacidad de mantener comunicación, intercambio y flujo constante de información y personas entre destinos, regiones y países.
        p La conectividad puede entenderse desde dos dimensiones interrelacionadas: física y digital.
        h3 Conectividad física 
        p La conectividad física se refiere a la existencia de infraestructuras de transporte adecuadas y seguras, junto con la disponibilidad de medios que faciliten la movilidad eficiente de los visitantes. Incluye carreteras, aeropuertos, puertos marítimos, terminales terrestres, rutas fluviales y servicios regulares de transporte.
        p Una buena conectividad física favorece la llegada de turistas, reduce los costos de viaje, estimula la competitividad de los destinos y fortalece la integración regional.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      h3 Ejemplos representativos: 
    
    .row.justify-content-center(data-aos="flip-down")
      .col-lg-10
        SlyderF(columnas="col-lg-6 col-xl-4")
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta1.svg' alt='AvatarTop')
            p.text-center Las rutas aéreas nacionales e internacionales que conectan Bogotá, Medellín, Cartagena y Cali posicionan a estas ciudades como nodos turísticos estratégicos.

          .tarjeta.color-acento-contenido.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta2.svg' alt='AvatarTop')
            p.text-center La mejora de carreteras como la Ruta del Sol o el Corredor del Pacífico impulsa el turismo interno y facilita el acceso a destinos emergentes.

          .tarjeta.color-secundario.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta3.svg' alt='AvatarTop')
            p.text-center En territorios insulares y fluviales, como San Andrés o el río Amazonas, la conectividad marítima y fluvial resulta esencial para el abastecimiento y el desarrollo sostenible del turismo.
          
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p No obstante, la conectividad física no se limita al transporte: también implica la integración logística entre aerolíneas, operadores terrestres, alojamientos y atractivos turísticos, garantizando una experiencia fluida, segura y eficiente para el visitante.
      h3 Conectividad digital
      p La conectividad digital se ha convertido en un factor determinante para la competitividad turística. Incluye el acceso a internet, las redes móviles, las plataformas digitales de promoción y reserva, así como las herramientas tecnológicas que facilitan la comunicación, gestión de datos y marketing turístico.
      p Según la Organización Mundial del Turismo (OMT, 2020), la conectividad digital amplía mercados, mejora la experiencia del viajero y fortalece la integración de los destinos en la economía global. En la actualidad, el turista digital espera poder buscar, planificar, reservar y compartir sus experiencias en línea, lo que exige a los destinos una adaptación tecnológica constante.

    .row.justify-content-center(data-aos="fade-right")
      .col-lg-11
        h3 Ejemplos y tendencias:

    .row.justify-content-center(data-aos="flip-up")
      .col-lg-10
        SlyderF(columnas="col-lg-6 col-xl-4")
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta4.svg' alt='AvatarTop')
            p.text-center La expansión de redes 4G y 5G, la disponibilidad de Wi-Fi gratuito en espacios públicos y la digitalización de servicios turísticos (reservas, pagos electrónicos, guías virtuales, mapas interactivos) son indicadores clave de conectividad.

          .tarjeta.color-acento-contenido.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta5.svg' alt='AvatarTop')
            p.text-center Los destinos con baja conectividad digital, especialmente en zonas rurales o territorios étnicos, enfrentan limitaciones en la promoción turística, la competitividad y la interacción con mercados internacionales.

          .tarjeta.color-secundario.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta6.svg' alt='AvatarTop')
            p.text-center Programas gubernamentales como “Conectividad Turística Digital Colombia”, liderados por el Ministerio de Comercio, Industria y Turismo, buscan reducir la brecha tecnológica y fortalecer la inclusión digital en regiones emergentes del país.
          
    br
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      h3 Conectividad integral y sostenibilidad
      p La conectividad, tanto física como digital, debe gestionarse bajo criterios de sostenibilidad, inclusión y equidad territorial. No basta con construir infraestructuras o ampliar redes tecnológicas; es indispensable que estos desarrollos se realicen respetando los ecosistemas, las culturas locales y las dinámicas socioeconómicas de las comunidades anfitrionas.
      p Una conectividad integral y sostenible contribuye a fortalecer la competitividad turística y la cohesión territorial del país, al mismo tiempo que impulsa la innovación y el bienestar comunitario. Entre sus principales aportes se destacan:

    .row.justify-content-center(data-aos="fade-right")
      .col-lg-10
        SlyderF(columnas="col-lg-6 col-xl-4")
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta7.svg' alt='AvatarTop')
            p.text-center Promueve la descentralización turística, facilitando el acceso a regiones menos desarrolladas sin degradar su entorno natural ni alterar su equilibrio sociocultural.

          .tarjeta.color-acento-contenido.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta8.svg' alt='AvatarTop')
            p.text-center Mejora la calidad de vida de las comunidades receptoras, al ampliar su acceso a servicios básicos, información y oportunidades económicas, fomentando la inclusión social.

          .tarjeta.color-secundario.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/tema1/bg-carrusel-tarjeta9.svg' alt='AvatarTop')
            p.text-center Favorece la resiliencia de los destinos frente a crisis globales (como la pandemia de COVID-19), gracias a la digitalización de procesos y la diversificación de mercados y canales de promoción.
             
</template>

